const util = require('../../util/util.js');
import refresh from '../../components/refresh.vue';
import navTab from '../../components/navTab.vue';
import tabBar4 from '../../components/tabBar4.vue';
import {formatTime} from '@/util/common'
export default {
	components: {
		refresh,
		navTab,
		tabBar4
	},
	data() {
		return {
			currentPage: 'index',
			toView: '', //回到顶部id
			tabTitle: ['全部', '未支付', '待接单', '待洗车', '进行中'], //导航栏格式 --导航栏组件
			currentTab: 0, //sweiper所在页
			pages: [1, 1, 1, 1, 1], //第几个swiper的第几页
			list: [
				[],
				[],
				[],
				[],
				[]
			] //数据格式
		};
	},
	onLoad(e) {

	},
	mounted() {
		//初始化数据
		let that = this;
		uni.showLoading({
			title: '加载中...',
			mask: true
		})
		for (let i = 0; i < that.pages.length; i++) {
			let status = that.orderState(i + 1);
			let orderListParam = {
				status: status,
				pageNum: that.pages[i],
				pageSize: 10
			}
			that.$api.request(orderListParam, 'api/order/orderList', 'GET', false).then(res => {
				let orderList = res.data.data.result;
				let newArr = orderList.map((item,index) =>{
					let myProperty = {
						formatCreateTime:formatTime(item.createTime),
						formatStatus:that.orderStatusStr(item.status)
					}
				   return Object.assign(item,myProperty)
				})
				console.log("订单数据：", newArr)
				
				let tempList = that.list
				tempList[i] = newArr
				that.list = tempList
				that.$forceUpdate() //二维数组，开启强制渲染
			})
		}
		setTimeout(() => {
			uni.hideLoading();
		}, 1000)
	},
	onShow() {},
	onHide() {},
	methods: {
		toTop() {
			this.toView = ''
			setTimeout(() => {
				this.toView = 'top' + this.currentTab
			}, 10)
		},
		changeTab(index) {
			this.currentTab = index;
		},
		naviagetorToPages(url) {
			uni.navigateTo({
				url: url
			});
		},
		// 其他请求事件 当然刷新和其他请求可以写一起 多一层判断。
		isRequest() {
			return new Promise((resolve, reject) => {
				this.pages[this.currentTab]++
				var that = this
				let status = that.orderState(that.currentTab + 1);
				let orderListParam = {
					status: status,
					pageNum: that.pages[that.currentTab],
					pageSize: 10
				}
				that.$api.request(orderListParam, 'api/order/orderList', 'GET').then(res => {
					let orderList = res.data.data.result;
					if (orderList == 0) {
						that.$msg('无更多数据')
					} else {
						let newArr = orderList.map((item,index) =>{
							let myProperty = {
								formatCreateTime:formatTime(item.createTime),
								formatStatus:that.orderStatusStr(item.status)
							}
						   return Object.assign(item,myProperty)
						})
						resolve(newArr)
					}
				})
			})
		},
		orderStatusStr(status){
			switch (status){
				case 0:
					return '待付款';
					break;
				case 1:
					return '待接单';
					break;
				case 2:
					return '进行中';
					break;
				case 3:
					return '未评价';
					break;
				default:
					return '';
					break;
			}
		},
		//订单状态
		orderState(curTab) {
			switch (curTab) {
				case 2:
					return 0; //待付款 
					break;
				case 3:
					return 1; //待接单
					break;
				case 4:
					return 2; //进行中
					break;
				case 5:
					return 3; //未评价
					break;
				default:
					return ""; //全部
					break;
			}
		},
		// swiper 滑动
		swiperTab: function(e) {
			var index = e.detail.current //获取索引
			this.$refs.navTab.longClick(index);
		},
		// 加载更多 util.throttle为防抖函数
		lower1: util.throttle(function(e) {
			console.log(`加载${this.currentTab}`) //currentTab表示当前所在页数 根据当前所在页数发起请求并带上page页数
			uni.showLoading({
				title: '加载中...',
				mask: true
			})
			this.isRequest().then((res) => {
				let tempList = this.list
				tempList[this.currentTab] = tempList[this.currentTab].concat(res)
				console.log(tempList)
				this.list = tempList
				this.$forceUpdate() //二维数组，开启强制渲染
			})
		}, 300),
		// 刷新touch监听
		refreshStart(e) {
			this.$refs.refresh.refreshStart(e);
		},
		refreshMove(e) {
			this.$refs.refresh.refreshMove(e);
		},
		refreshEnd(e) {
			this.$refs.refresh.refreshEnd(e);
		},
		//刷新
		isRefresh() {
			let that = this;
			let status = that.orderState(that.currentTab + 1);
			let orderListParam = {
				status: status,
				pageNum: that.pages[that.currentTab],
				pageSize: 10
			}
			that.$api.request(orderListParam, 'api/order/orderList', 'GET').then(res => {
				let orderList = res.data.data.result;
				let newArr = orderList.map((item,index) =>{
					let myProperty = {
						formatCreateTime:formatTime(item.createTime),
						formatStatus:that.orderStatusStr(item.status)
					}
				   return Object.assign(item,myProperty)
				})
				
				let tempList = that.list
				tempList[that.currentTab] = newArr
				that.list = tempList
				that.$forceUpdate() //二维数组，开启强制渲染
				setTimeout(() => {
					uni.showToast({
						icon: 'success',
						title: '刷新成功'
					})
					that.$refs.refresh.endAfter() //刷新结束调用
				}, 1000)
			})
		}
	}
};
